.active-cell {
	display: flex;
	flex-direction: column;
	justify-content: space-around;

		background-color: #463a2f;
	text-decoration: none;
	height: 55px;
	padding-left: 30px;
}

.inactive-cell {
	display: flex;
	flex-direction: column;
	justify-content: space-around;

	color: #ffffff !important;
	text-decoration: none;
	height: 55px;
	padding-left: 30px;
}

.seperator {
	height: 1px;
	width: 100%;
	background-color: #190e77;
}

.emp-strip {
	margin-bottom: 1px;
	background-color: #aaaaaa;
	height: 30px;
}

.relative {
	position: relative;
	z-index: 3;
}

.purple-bg {
	position: fixed;
	top: 0;
	left: 0;
	background-color: #6c59fae0;
	z-index: -1;
	width: 100%;
	height: 100%;
}

.tvalign {
	vertical-align: middle;
}

.pop-image {
	position: fixed;
	top: 0;
	left: 0;
	background-image: url("../../img/image_popup.png");
	background-repeat: no-repeat;

	z-index: 0;
	width: 100%;
	height: 100%;
}

.login-btn {
	font-family: "FiraSans", sans-serif;
	padding: 5px 30px 5px;
	background-color: #ffffff;
	color: #1d1d1d !important;
	border-radius: 3em;
	font-size: 16px;
	border: 2px solid #8d8d8d;
	margin-top: 10px;
	margin-left: 40px;
	margin-right: auto;
	margin-bottom: 120px;
}

.login-btn2 {
	font-family: "FiraSans", sans-serif;
